import React from 'react';
import { SocialIcon } from 'react-social-icons'
import { I18nProvider } from '../i18nProvider';
import { StaticImage } from "gatsby-plugin-image"
import translate from '../i18nProvider/translate';

const Footer = (props) => { 

    var privacyPolicyUrl = "https://igtracker.app/" + props.locale + "/privacy-policy/"
    return (
      <I18nProvider locale={props.locale}>
        <footer className="page-footer">
          <div className="container">
            <div className="row">
              <div className="col l6 s12">
                <h3 className="black-text">{translate('vacama-product')}</h3>
                <StaticImage src="../images/vakma.webp" alt="Ig Tracker logo" />
              </div>
              <div className="col l5 offset-l1  s12">
                <h3 className="black-text">{translate('contact')}</h3>
                <div className="row hide-on-med-and-down show-on-large">
                  <div className="col s2" >
                    <SocialIcon  url="https://www.instagram.com/vacamacompany/" rel="nofollow noreferrer noopener noindex" network='instagram' bgColor="black" target="_blank" style={{ height: "3vmax", width: "3vmax"}}/>
                  </div>
                  <div className="col s10">
                    <p id="footerContactDescription">{translate('follow-us')}</p>
                  </div>
                  <div className="col s2">
                    <SocialIcon url="mailto:vacamacompany@gmail.com" network='email' bgColor="#B91919" style={{ height: "3vmax", width: "3vmax" }}/>
                  </div>
                  <div className="col s10">
                    <p id="footerContactDescription">vacamacompany@gmail.com</p>
                  </div>
                </div>
                <div className="row show-on-medium-and-down hide-on-large-only">
                  <div className="col s1" >
                    <SocialIcon  url="https://www.instagram.com/vacamacompany/" rel="nofollow noreferrer noopener noindex" network='instagram' bgColor="black" target="_blank" style={{ height: "3vmax", width: "3vmax" }}/>
                  </div>
                  <div className="col s11">
                    <p id="footerContactDescription">{translate('follow-us')}</p>
                  </div>
                  <div className="col s1">
                    <SocialIcon url="mailto:vacamacompany@gmail.com" network='email' bgColor="#B91919" style={{ height: "3vmax", width: "3vmax" }}/>
                  </div>
                  <div className="col s11">
                    <p id="footerContactDescription">vacamacompany@gmail.com</p>
                  </div>
              </div>
              </div>
            </div>
          </div>
          <div className="footer-copyright">
            <div className="container">
             2020 Vacama Company 
            <a className="grey-text text-lighten-4 right" href={privacyPolicyUrl} rel="nofollow noreferrer noopener noindex">{translate('privacy-policy')}</a>
            </div>
          </div>
        </footer>
      </I18nProvider>
    );
}

export default Footer;